import React from 'react';
import { Link } from 'react-router-dom';
import { LocationMarkerIcon } from '@heroicons/react/outline';

const SignUp = () => {
    return (
      <div class="flex items-center justify-center h-screen w-full">
      <div class="px-4 py-6 mx-auto mt-4 text-left bg-zinc-100 rounded-lg  shadow-lg md:w-1/3 lg:w-1/3 ">
          <div class="flex justify-center">
              <svg xmlns="http://www.w3.org/2000/svg" class="w-20 h-20 text-indigo-700" fill="none" viewBox="0 0 24 24"
                  stroke="currentColor">
                  <path d="M12 14l9-5-9-5-9 5 9 5z" />
                  <path
                      d="M12 14l6.16-3.422a12.083 12.083 0 01.665 6.479A11.952 11.952 0 0012 20.055a11.952 11.952 0 00-6.824-2.998 12.078 12.078 0 01.665-6.479L12 14z" />
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
                      d="M12 14l9-5-9-5-9 5 9 5zm0 0l6.16-3.422a12.083 12.083 0 01.665 6.479A11.952 11.952 0 0012 20.055a11.952 11.952 0 00-6.824-2.998 12.078 12.078 0 01.665-6.479L12 14zm-4 6v-7.5l4-2.222" />
              </svg>
          </div>
          <h3 class="text-2xl font-bold text-center">Join us</h3>
          <form action="">
              <div class="mt-4">
                  <div>
                      <label class="block" for="Name">Name</label>
                              <input type="text" placeholder="Name"
                                  class="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-1 focus:ring-blue-600"/>
                  </div>
                  <div class="mt-4">
                      <label class="block" for="email">Email</label>
                              <input type="text" placeholder="Email"
                                  class="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-1 focus:ring-blue-600"/>
                  </div>
                  <div class="mt-4">
                      <label class="block">Password</label>
                              <input type="password" placeholder="Password"
                                  class="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-1 focus:ring-blue-600"/>
                  </div>
                  <div class="mt-4">
                      <label class="block">Confirm Password</label>
                              <input type="password" placeholder="Password"
                                  class="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-1 focus:ring-blue-600"/>
                  </div>
                  <span class="text-xs text-red-400">Password must be same!</span>
                  <div class="flex">
                  <button className="block w-full p-3 text-center rounded text-white hover:bg-indigo-600 bg-indigo-700 duration-200 hover:-translate-y-1  hover:scale-110 transition-al">Create Account</button>
                  </div>
                  <div class="mt-6 text-grey-dark">
                      Already have an account?
                      <Link class="text-indigo-700 hover:underline" to='login'>
                          Log in
                      </Link>
                  </div>
              </div>
          </form>
      </div>
  </div>
    );
};

export default SignUp;