

import React, {useState} from 'react';

import { MenuIcon, XIcon } from '@heroicons/react/outline';
import { Link } from 'react-router-dom';
import  './Header.css';




const Header = () => {
    const [nav, setNav] = useState(false)
    const handleClick = () => setNav(!nav)

    const handleClose =()=> setNav(!nav)

    return (
        <div className='conainer top-0 sticky mx-auto h-[80px] z-10 bg-white  drop-shadow-lg'>
        <div className='px-8  flex lg:justify-around justify-between items-center w-full h-full'>
          <div className='flex items-center md:justify-between  '>
            <Link to='/'><img src="https://i.ibb.co/jHVdYBd/20220617-224257-0000-01.png" className='lg:h-10 lg:w-12 h-5 w-6 mr-3' alt="" /></Link> 
          <Link to='/' className="cursor-pointer text-xl md:text-3xl lg:text-4xl f">Quick <span className="text-indigo-700 blur-3">Solution</span> </Link> 
            <ul className='hidden  lg:flex ml-10 font-semibold'>
                <li><Link to='/' className=" hover:text-indigo-700  ">Home</Link></li>
                 <li><Link to='findjobs' className="hover:text-indigo-700  md:ml-4">Find-Jobs</Link></li>
                 <li><Link to='jobCatagory' className="hover:text-indigo-700 md:ml-4  ">Job-Catagory</Link></li>
                 <li><Link to='about' className="hover:text-indigo-700 md:ml-4 "> About</Link></li>
            </ul>
          </div>
          <div className='hidden lg:flex  '>
            <Link to='login' className="hover:text-indigo-700 mt-2 mr-2">
              Log In
            </Link>
            <Link className=" transition text-white ease-in-out delay-150 bg-indigo-700 hover:-translate-y-1 hover:text-white hover:scale-110 hover:bg-indigo-500 duration-300 px-4 py-2 rounded" to='signup'>Sign Up</Link>
          </div>
          <div className='lg:hidden mr-4' onClick={handleClick}>
              {!nav ? <MenuIcon className='w-5 transition ease-in-out delay-150  hover:-translate-y-1 hover:scale-110 hover:text-indigo-700 duration-300' /> : <XIcon className='w-5 transition ease-in-out delay-150  hover:-translate-y-1 hover:scale-110 hover:text-indigo-700 duration-300' />}
            
          </div>
        </div>
  
        <ul className={!nav ? 'hidden' : 'absolute bg-zinc-200 w-full px-8 transform duration-300 '}>
                 <li><Link to='/' className="hover:text-indigo-700 ">Home</Link></li>
                 <li><Link to='findjobs' className="hover:text-indigo-700  ">Find-Jobs</Link></li>
                 <li><Link to='jobCatagory' className="hover:text-indigo-700  ">Job-Catagory</Link></li>
                 <li><Link to='about' className="hover:text-indigo-700 "> About</Link></li>
                
          
          <div className='flex  my-4'>
                <li> <Link to='login' className="hover:text-indigo-700  ">Login</Link></li>
                 <li> <Link to='signup' className="duration-300   bg-indigo-700 px-2 py-2  rounded-md text-white text-sm ml-2  hover:bg-indigo-600 transition-all">Sign Up</Link></li>
          </div>
        </ul>
      </div>
    );
};

export default Header;